	module.exports = {"data":{"post":{"html":"<p>Or dead com. Or dot zombie. I don't know which of these we are, but Mama now reminds me daily that she achieves our yearly worldwide sales every 3 weeks or so in northern Germany alone. Now she gets to make executive decisions like who gets the new office chairs, and what to do when when too many PCs are ordered.</p>\n<p>And like a bad penny, CeBIT stress has reared it's ugly head again. Why take the time to do something right, when something, <i>anything</i> has to be ready by March 22? But don't tell me about until a week beforehand. </p>\n<p>At day care Christopher refuses to drink the fruit tea we send along, holding out for apple juice. But at home he drinks the fruit tea. I guess he's already practicng manipulation of adults.</p>","fields":{"title":"Dot Bomb","date":"15 Mar 2001","url":"https://papascott.de/2001-03-15-dot-bomb/"}}},"pathContext":{"slug":"/2001-03-15-dot-bomb/","prev":{"fields":{"title":"Sweet dreams are made of this","date":"14 Mar 2001","slug":"/2001-03-14-sweet-dreams-are-made-of-this/"}},"next":{"fields":{"title":"Breaking Things","date":"17 Mar 2001","slug":"/2001-03-17-breaking-things/"}}}}

